import { Navbar, Nav, Container } from "react-bootstrap";
import React, { useEffect, useState, useContext, useMemo } from "react";
import { withRouter } from "react-router";
import { NavLink } from "react-router-dom";
import styled, { ThemeContext } from "styled-components";
import endpoints from "../constants/endpoints";
import ThemeToggler from "./ThemeToggler";

const styles = {
    logoStyle: {
        width: 50,
        height: 40,
    },
};

const ExternalNavLink = styled.a`
    color: ${(props) => props.theme.navbarTheme.linkColor};
    &:hover {
        color: ${(props) => props.theme.navbarTheme.linkHoverColor};
    }
    &::after {
        background-color: ${(props) => props.theme.accentColor};
    }
`;

const getLinkColor = (theme, path) => {
    switch (path) {
        case "/about":
            return theme.aboutColor;
        case "/education":
            return theme.educationColor;
        case "/experiences":
            return theme.experiencesColor;
        case "/projects":
            return theme.projectsColor;
        case "/skills":
            return theme.skillsColor;
        default:
            return theme.navbarTheme.linkColor;
    }
};

const InternalNavLink = styled(NavLink)`
    color: ${(props) => props.theme.navbarTheme.linkColor};
    &:hover {
        color: ${(props) => props.theme.navbarTheme.linkHoverColor};
    }
    &::after {
        background-color: ${(props) => getLinkColor(props.theme, props.to)};
    }
    &.navbar__link--active {
        color: ${(props) => props.theme.navbarTheme.linkActiveColor};
    }
`;

const NavBar = () => {
    const theme = useContext(ThemeContext);
    const [data, setData] = useState(null);
    const [expanded, setExpanded] = useState(false);

    useEffect(() => {
        fetch(endpoints.navbar, {
            method: "GET",
        })
            .then((res) => res.json())
            .then((res) => setData(res))
            .catch((err) => {
                console.error("Failed to fetch navbar data:", err);
                setData({ sections: [] });
            });
    }, []);

    const sections = useMemo(() => data?.sections || [], [data]);

    return (
        <Navbar
            fixed="top"
            expand="md"
            bg="dark"
            variant="dark"
            className="navbar-custom"
            expanded={expanded}
        >
            <Container>
                {data?.logo && (
                    <Navbar.Brand href="/">
                        <img
                            src={data?.logo?.source}
                            className="d-inline-block align-top"
                            alt="main logo"
                            style={
                                data?.logo?.height && data?.logo?.width
                                    ? {
                                          height: data?.logo?.height,
                                          width: data?.logo?.width,
                                      }
                                    : styles.logoStyle
                            }
                        />
                    </Navbar.Brand>
                )}
                <Navbar.Toggle
                    aria-controls="responsive-navbar-nav"
                    onClick={() => setExpanded(!expanded)}
                />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto" />
                    <Nav>
                        {sections.map((section, index) =>
                            section?.type === "link" ? (
                                <ExternalNavLink
                                    key={section.title}
                                    href={section.href}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    onClick={() => setExpanded(false)}
                                    className="navbar__link"
                                    theme={theme}
                                >
                                    {section.title}
                                </ExternalNavLink>
                            ) : (
                                <InternalNavLink
                                    key={section.title}
                                    onClick={() => setExpanded(false)}
                                    exact={index === 0}
                                    activeClassName="navbar__link--active"
                                    className="navbar__link"
                                    to={section.href}
                                    theme={theme}
                                >
                                    {section.title}
                                </InternalNavLink>
                            )
                        )}
                    </Nav>
                    <ThemeToggler onClick={() => setExpanded(false)} />
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

const NavBarWithRouter = withRouter(NavBar);
export default NavBarWithRouter;
